*{
    margin: 0;

    padding: 0;
}


.logo-img{

    height: 80px;
    width: 150px;
    margin-right: 550px;
}


.head-main{

    display: flex;
    justify-content: space-around;
    align-items: center;
    /* border: 1px solid black; */
    padding: 5px;

    border-bottom: 1px solid rgb(231, 228, 228);
    height: 5vw;
    position: sticky;
    top: 0;
    background-color: #fff;
    z-index: 19;
}

.nav-box{

    margin-right: 50px;
}

.nav-links{

    display: flex;
    justify-content: space-between;
    gap: 80px;

    margin-right: 20px;
    list-style-type: none;

    font-size: medium;
    font-weight: 600;

    cursor: pointer;
    
}

.head-links{
    text-decoration: none;
    color: black;
}



.login-btn{

    position: absolute;

    height: 30px;
    width: 100px;

    font-weight: 700;

    background-color: rgb(255, 255, 255);

    /* border: 1px solid black; */

    text-align:center;

    /* border-radius: 9px; */

    top: 34px;

    right: 55px;

    cursor: pointer;



}


.login-box i{

    position: absolute;
   right: 135px;
   top: 40px;
   z-index: 20;
   cursor: pointer;
}


.body-main{

    display: flex;
    flex-wrap: wrap;


    justify-content: center;
    align-items: center;

    gap: 50px;
    
    padding: 80px 80px ;

    line-height: 5px;

    background-color: #F2F2F3;
 

    /* border: 1px solid black; */
    /* height: 100vh; */
}



.search-box input{

    width: 300px;
    height: 30px;

}


.search-box i{


    height: 30px;
    position: absolute;
    right: 400px;
    top: 10px;
    opacity: 0.5;

}


.search-box{
    position: absolute;
    display: flex;

    gap: 30px;

    top: 100px;
}

.search-box button{

    margin-left: 50px;

    cursor: pointer;
}

   




.rescard-main{
    border: 1px solid black;
    /* height: 300px; */
    width: 250px;
    border-radius: 10px;
    cursor: pointer;
    overflow: hidden;
    color: black;
 


    
}

.rescard-main img:hover{

    opacity: 1;


    
}



.res-details{
    padding: 10px;
    line-height: 30px;
    
}

.rescard-main img{

    object-fit: cover;
    height:160px;
    width: 250px;
    border-radius: 9px;
    opacity: 0.8;
    
}





.rate-time{
    display: flex;
    gap: 80px;
    line-height: 40px;
    /* justify-content: center; */
    
}




.footer-main{
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F0F0F5;
    gap: 10px;
}


.footer-main .img1{

    height: 65px;
    width: 150px;
    margin-right: 20px;
    padding: 10px;

    cursor: pointer;
    
}

.footer-main .img2{

    height: 65px;
    width: 150px;

    cursor: pointer;
    
}

.footer-main h3{

    font-weight: 700;
    font-size: 33px;
}



.Shimmer-card{

    border: 1px solid black;
    height: 300px;
    width: 250px;
    border-radius: 10px;

}

.Shimmer-card img{

    object-fit: cover;
    height:160px;
    width: 250px;
    border-radius: 9px;
    background-color: #94949c;
    
}


.shimmer-main{

    display: flex;

    flex-wrap: wrap;

    gap: 55px;

    padding: 120px;
}



.link-body{
    text-decoration: none;
    color: black;
}


.menu-main{
    padding: 20px 500px;
}

.menu-main li{
    list-style-type: upper-roman;
}


.title{
    display: flex;
}


.main-about{

height: 20vh;

      & h1{
         
        display: flex;
        justify-content: center;
        align-items: center;

        margin-top: 30px;

      }

}


.about1{
    padding: 20px;
    border: 2px solid black;
    width: fit-content;
    margin: 20px;
}

.about2{
    padding: 20px;
    border: 2px solid black;
    width: fit-content;
    margin: 20px;
}

.status{

    padding: 20px;
    margin: 20px;
}